  components: {
    'comp-tab': tab
  },
  name: 'order-list',
  watch: {
    'params.status': function() {
      this.list = [];
      this.getOrders();
    }
  },
  data() {
    return {
      tabs: [{ label: '全部订单', value: -1 }, { label: '未支付', value: 0 }, { label: '拼团中', value: 1 }, { label: '待发货', value: 2 }, { label: '待收货', value: 3 }, { label: '已收货', value: 4 }, { label: '团购失败', value: 5 }, { label: '申请退款', value: 12 }, { label: '申请退货', value: 9 }, { label: '已取消', value: 6 }],
