  },
  created() {
    this.token = getToken();
    rpc(
      'StoreDeliveryService.GetDeliveryList',
      M('DeliveryListQueryRequest', {
        token: M('Token', { token: this.token }),
        count: 100
      })
    ).then(res => {
      var l = [];
      res.deliverylistList.forEach(v => {
        if (v.deliveryid === 'BEST') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '标准快递'
          });
        }
        if (v.deliveryid === 'DB') {
          l.push({
            name: `${v.deliveryname}-大件快递3.60(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-大件快递3.60`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '大件快递3.60'
          });
          l.push({
            name: `${v.deliveryname}-特准快件(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-特准快件`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 2,
            servicevalue: '特准快件'
          });
        }
        if (v.deliveryid === 'EMS') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 6,
            servicevalue: '标准快递'
          });
          l.push({
            name: `${v.deliveryname}-快递包裹(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-快递包裹`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 9,
            servicevalue: '快递包裹'
          });
        }
        if (v.deliveryid === 'OTP') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '标准快递'
          });
        }
        if (v.deliveryid === 'PJ') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '标准快递'
          });
        }
        if (v.deliveryid === 'SF') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 0,
            servicevalue: '标准快递'
          });
          l.push({
            name: `${v.deliveryname}-顺丰即日(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-顺丰即日`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '顺丰即日'
          });
          l.push({
            name: `${v.deliveryname}-顺丰次晨(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-顺丰次晨`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 2,
            servicevalue: '顺丰次晨'
          });
          l.push({
            name: `${v.deliveryname}-顺丰标快(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-顺丰标快`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 3,
            servicevalue: '顺丰标快'
          });
          l.push({
            name: `${v.deliveryname}-顺丰特惠(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-顺丰特惠`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 4,
            servicevalue: '顺丰特惠'
          });
        }
        if (v.deliveryid === 'STO') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '标准快递'
          });
        }
        if (v.deliveryid === 'YTO') {
          l.push({
            name: `${v.deliveryname}-普通快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-普通快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 0,
            servicevalue: '普通快递'
          });
          l.push({
            name: `${v.deliveryname}-圆准达(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-圆准达`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '圆准达'
          });
        }
        if (v.deliveryid === 'YUNDA') {
          l.push({
            name: `${v.deliveryname}-标准快件(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快件`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 0,
            servicevalue: '标准快件'
          });
        }
        if (v.deliveryid === 'ZTO') {
          l.push({
            name: `${v.deliveryname}-标准快件(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快件`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 0,
            servicevalue: '标准快件'
          });
        }
      });
      l.push({
        name: '德邦快递-大件快递3.60(现付)',
        shippingcompany: '德邦快递-大件快递3.60',
        deliveryid: 'DB',
        bizid: 'DB_CASH',
        servicetype: 1,
        servicevalue: '大件快递3.60'
      });
      l.push({
        name: '德邦快递-特准快件(现付)',
        shippingcompany: '德邦快递-特准快件',
        deliveryid: 'DB',
        bizid: 'DB_CASH',
        servicetype: 2,
        servicevalue: '特准快件'
      });

      /*
      l.push({
        name: '承诺达特快-标准快递(现付)',
        shippingcompany: '承诺达特快-标准快递',
        deliveryid: 'OTP',
        bizid: 'OTP_CASH',
        servicetype: 1,
        servicevalue: '标准快递'
      });

      l.push({
        name: '品骏快递-标准快递(现付)',
        shippingcompany: '品骏快递-标准快递',
        deliveryid: 'PJ',
        bizid: 'PJ_CASH',
        servicetype: 1,
        servicevalue: '标准快递'
      });
        */

      l.push({
        name: '顺丰速运-标准快递(现付)',
        shippingcompany: '顺丰速运-标准快递',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 0,
        servicevalue: '标准快递'
      });
      l.push({
        name: '顺丰速运-顺丰即日(现付)',
        shippingcompany: '顺丰速运-顺丰即日',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 1,
        servicevalue: '顺丰即日'
      });
      l.push({
        name: '顺丰速运-顺丰次晨(现付)',
        shippingcompany: '顺丰速运-顺丰次晨',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 2,
        servicevalue: '顺丰次晨'
      });
      l.push({
        name: '顺丰速运-顺丰标快(现付)',
        shippingcompany: '顺丰速运-顺丰标快',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 3,
        servicevalue: '顺丰标快'
      });
      l.push({
        name: '顺丰速运-顺丰特惠(现付)',
        shippingcompany: '顺丰速运-顺丰特惠',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 4,
        servicevalue: '顺丰特惠'
      });
      l.push({
        name: '商家自配送',
        shippingcompany: '商家自配送',
        deliveryid: '',
        bizid: '',
        servicetype: 0,
        servicevalue: ''
      });
      this.shiplist = l;
    });
    this.getOrders();

    this.conn = null;
    const listen = () => {
      if (this.conn === -1) {
        return;
      }
      this.conn = invoke(
        'StoreNotificationService.GetNotification',
        M('Token', { token: this.token }),
        async res => {
          if (res.kind !== 1) {
            return;
          }
          var item = res.order;
          const reslist = await Promise.all([
            rpc1(
              'StoreUserService.GetUser',
              M('IDWithTokenRequest', {
                token: M('Token', { token: this.token }),
                id: item.userid
              })
            ),
            rpc1(
              'StoreProductService.GetProductList',
              M('ProductListQueryRequest', {
                token: M('Token', { token: this.token }),
                idlistList: item.orderproductsList.map(v => v.productid),
                sortby: 1,
                sort: 1,
                count: item.orderproductsList.map(v => v.productid).length
              })
            )
          ]);
          item.user = reslist[0];
          item.orderproductsList.forEach(item1 => {
            item1.product = reslist[1].productlistList.find(v => v.id === item1.productid);
          });
          this.list.splice(0, 0, item);
        },
        e => {
          console.log(e);
          setTimeout(listen, 2000);
        }
      );
    };
    listen();
  },
  beforeDestroy() {
    this.conn.close();
    this.conn = -1;
  },
  methods: {
    // 订单列表
    async getOrders() {
      this.loadmore = true;
      const { scrollY } = window;
      this.params.createdatgreaterthan = 0;
      this.params.createdatlessthan = 0;
      if (this.params.time.length === 2) {
        this.params.createdatgreaterthan = parseInt(this.params.time[0].getTime() / 1000);
        this.params.createdatlessthan = parseInt(this.params.time[1].getTime() / 1000);
      }
      this.params.isgroup = 0;
      if (this.params.ordertype != 0) {
        if (this.params.ordertype == 1) {
          this.params.isgroup = 1;
        }
      }
      // 根据手机号获取用户id
      if (this.params.delivermobile) {
        const res = await rpc(
          'StoreUserService.GetUserList',
          M('UserListQueryRequest', {
            token: M('Token', { token: this.token }),
            mobile: this.params.delivermobile,
            count: 1,
            order: 0
          })
        );
        this.params.userid = res.userlistList.length ? res.userlistList[0].id : 0;
      }
      // 获取订单列表
      this.params.idlessthan = this.list.length ? this.list[this.list.length - 1].id : 0;
      const res = await rpc(
        'StoreOrderService.GetOrderList',
        M('OrderListQueryRequest', {
          token: M('Token', { token: this.token }),
          ...this.params
        })
      );
      if (res.orderlistList.length === 0) {
        this.loadmore = false;
        this.hasMore = false;
        return;
      }
      // 完善订单列表里的关联资源
      const useridlist = res.orderlistList.map(item => item.userid);
      const productidlist = [];
      res.orderlistList.forEach(item => {
        item.orderproductsList.forEach(item1 => {
          productidlist.push(item1.productid);
        });
      });
      const reslist = await Promise.all([
        rpc(
          'StoreUserService.GetUserList',
          M('UserListQueryRequest', {
            token: M('Token', { token: this.token }),
            idlistList: useridlist,
            count: useridlist.length,
            order: 0
          })
        ),
        rpc(
          'StoreProductService.GetProductList',
          M('ProductListQueryRequest', {
            token: M('Token', { token: this.token }),
            idlistList: productidlist,
            sortby: 1,
            sort: 1,
            count: productidlist.length
          })
        )
      ]);
      res.orderlistList.forEach(item => {
        item.user = reslist[0].userlistList.find(v => v.id === item.userid);
        item.orderproductsList.forEach(item1 => {
          item1.product = reslist[1].productlistList.find(v => v.id === item1.productid);
        });
      });
      this.list = this.list.concat(res.orderlistList);
      this.hasMore = res.orderlistList.length;
      this.loadmore = false;
      if (this.params.idlessthan !== 0) {
        window.scroll(0, scrollY);
      }
    },

    updateOrder(row, status) {
      row = JSON.parse(JSON.stringify(row));
      if (status == 3) {
        this.shippingrow = row;
        this.dialogFormVisible = true;
        return;
      }
      row.status = status;
      rpc(
        'StoreOrderService.UpdateOrder',
        M('OrderWithTokenRequest', {
          token: M('Token', { token: this.token }),
          order: M('Order', {
            ...row,
            address: M('Address', row.address),
            invoice: M('Invoice', row.invoice),
            orderproductsList: row.orderproductsList.map(v => M('OrderProduct', v))
          })
        })
      ).then(res => {
        this.list.splice(this.list.findIndex(v => v.id === res.id), 1, row);
        this.$message({
          message: '操作成功',
          type: 'success'
        });
      });
    },
    updateOrderShipping() {
      const row = JSON.parse(JSON.stringify(this.shippingrow));
      row.status = 3;
      row.shippingcompany = this.shiplist[this.shipindex].shippingcompany;
      rpc(
        'StoreOrderService.UpdateOrder',
        M('OrderWithTokenRequest', {
          token: M('Token', { token: this.token }),
          order: M('Order', {
            ...row,
            address: M('Address', row.address),
            invoice: M('Invoice', row.invoice),
            orderproductsList: row.orderproductsList.map(v => M('OrderProduct', v))
          }),
          ...this.shiplist[this.shipindex]
        })
      ).then(res => {
        this.list.splice(this.list.findIndex(v => v.id === res.id), 1, row);
        this.dialogFormVisible = false;
        this.$message({
          message: '操作成功',
          type: 'success'
        });
      });
    },

    onClickFilter() {
      this.list = [];
      this.getOrders();
    },
    onClickClearFilter() {
      this.params = {
        id: '',
        status: -1,
        statuslistList: null,
        idlessthan: 0,
        createdatgreaterthan: 0,
        createdatlessthan: 0,
        sortby: 1,
        sort: 1,
        deliverkind: 0,
        desk: '',
        daynumber: '',
        count: 20,
        delivermobile: '',
        isgroup: 0,
        issubscription: 0,

        ordertype: 0,
        time: []
      };
      this.list = [];
      this.getOrders();
    }
  }
};
</script>

